function formatUnit(){
        return function(num) {
                if (num >= 1024 * 1024 * 1024 * 1024) { //bytes format to TB
                    return (num / (1024 * 1024 * 1024 * 1024)).toFixed(1) + 'TB';
                } else if (num >= 1024 * 1024 * 1024) { //bytes format to GB
                    return (num / (1024 * 1024 * 1024)).toFixed(1) + 'GB';
                } else if (num >= 1024 * 1024) { //bytes format to MB
                    return (num / (1024 * 1024)).toFixed(1) + 'MB';
                } else if (num >= 1024) { //bytes format to KB
                    return (num / 1024).toFixed(1) + ' KB';
                } else if (num < 1024) { //show bytes
                    return num.toFixed(1) + ' Bytes';
                }
            }
}


angular.module('hz.dashboard.lenovo.network_switches')
    .filter('formatUnit', formatUnit );



