import Base$httpService from '../base$httpService.js';
import config from '../../core/initConfig';


class HostService extends Base$httpService {



    // testPost(url, params) {
    //     return this.post(url, params);
    // }

    // testGet(url, params) {
    //     return this.get(url, params);
    // }

    getCluster(url,params){
        return this.get(url, params);
    }

    getHostList(url,params){
        return this.get(url+'?cluster_id='+params.cluster_id+'&marker='+params.marker+'&pagesize='+params.pagesize);
    }

    getServerOsdList(url,params){
        return this.get(url, params);
    }

    getServerDiskList(url,params){
        return this.get(url, params);
    }

}



angular.module(config.name)
    .service('hostService', HostService);


