import Base$httpService from '../base$httpService.js';
import config from '../../core/initConfig';


class PoolService extends Base$httpService {

    getCluster(url,params){
        return this.get(url, params);
    }

    getPoolList(params) {
       return this.get(config.host + "/api/storage/clusters/" + params.cluster_id + "/pools");
    }

}

angular.module(config.name)
    .service('poolService', PoolService);


