import config from '../../../core/initConfig.js'

class HostTableController {
    //inject you want to dependencies ,and add them to constructor as pramaters above
    //note:the order need match
    static $inject = ['$scope', 'ceph.host.action', 'hostService','horizon.framework.widgets.toast.service'];

    constructor(scope, action, hostService,toastService) {
        this.action = action;
        this.hostService = hostService;
        this.toastService = toastService;
        this.init(scope);

        scope.showPagination = function () {
            // get $$childHead first and then iterate that scope's $$nextSiblings
            var parentScope = angular.element('#hosts_pagination').scope();

            for (var cs = parentScope.$$childHead; cs; cs = cs.$$nextSibling) {
                if (cs.pages && cs.pages.length > 1) {
                    return true;
                }
            }
            return false;
        };

    };

    /**
     * query cluster id
     */
    getClusterInfo() {
        return this.hostService.getCluster(config.host + "/api/storage/clusters/")
    }

    /**
     * query hostlist
     * @param {Object} params cluster_id,page offset,current page numbers
     */
    getHostList(scope, params) {
        let self = this;
        self.hostService.getHostList(config.host + "/api/storage/servers", params)
            .success((result) => {
                // angular.element('#hosts_all_checkbox').scope().specialReset();
                scope.hosts = result.data;
                scope.hostState = true;
            })
            .error((err) => {
                toastService.add('error', gettext('Unable to get ceph Hosts info.'));
                // console.log(err);
            });
    }

    /**
     * init page data
     */
    initData(scope) {
        let self = this;
        self.getClusterInfo().success((result) => {
            result && result.data && result.data.length > 0 &&
            self.getHostList(scope, {cluster_id: result.data[0].id, marker: 0, pagesize: 999});
        })
            .error((err) => {
                toastService.add('error', gettext('Unable to get ceph cluster info'));
                // console.log(err);
            });
    }

    reset = function (scope) {
        let self = this;
        scope.ihosts = [];
        scope.hosts = [];
        scope.hostState = false;

        scope.host = [];
        scope.nodes = [];

        //scope.checked = {};
        //scope.selected = {};
        if (scope.selectedData) {
            scope.selectedData.aData = [];
        }
    };

    init(scope) {
        let self = this;
        scope.isAdding = false;
        scope.isEditting = false;
        scope.isDeleting = false;

        self.setPageConfig(scope);
        self.bindAction(scope);
        self.initData(scope);
    }

    bindAction(scope) {
        let self = this;

        scope.actions = {
            refresh: self.initData.bind(this, scope),
            modal: new self.action(scope)
        };
    }

    setPageConfig(scope) {
        let self = this;
        scope.context = {
            header: {
                serverName: gettext('Host Name'),
                publicIp: gettext('Public IP'),
                clusterIp: gettext('Cluster IP'),
                role: gettext('Role'),
                Status: gettext('Status'),
                nodata: gettext('No DATA'),
            },
            action: {
                // create: gettext('Add Switch'),
                // edit: gettext('Edit'),
                // delete: gettext('Delete Switches')
            },
            error: {
                api: gettext('Unable to retrieve imagess'),
                priviledge: gettext('Insufficient privilege level to view user information.')
            }
        };

        scope.filterFacets = [{
            label: gettext('Server Name'),
            name: 'servername',
            singleton: true
        },
            {
                label: gettext('Public IP'),
                name: 'publicip',
                singleton: true
            },
            {
                label: gettext('Cluster IP'),
                name: 'clusterip',
                singleton: true
            },
            {
                label: gettext('Role'),
                name: 'hw_type',
                singleton: true
            },
            {
                label: gettext('Status'),
                name: 'status',
                singleton: true
            }
        ];

    }
}


module.exports = HostTableController;
// angular.module(config.name)
